<template>
  <div class="form-elements">
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.questions.create' | translate">
          <div class="row">
            <div class="col">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('title')}">
                    <div class="input-group">
                      <input
                        id="title"
                        name="title"
                        v-model="title"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="title">{{'question.title'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('title')"
                             class="help text-danger">{{
                        errors.first('title')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <vuestic-simple-select
                    :label="'question.question_type' | translate"
                    v-validate="'required'"
                    v-model="question_typeItem"
                    option-key="title"
                    v-bind:options="this.question_type">
                  </vuestic-simple-select>
                </div>

                <div class="col">
                  <vuestic-multi-select
                    v-model="categoriesItems"
                    option-key="title"
                    v-bind:options="this.category_id"
                    :label="'question.questions' | translate">
                    </vuestic-multi-select>
                </div>                
                
              </div>
            </div>
          </div>

          <div class="row row-reverse mb-4">
            <div class="col-md-8 text-right">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('title')}">              
                    <div class="input-group">
                      <input
                        id="question"
                        name="question"
                        v-model="question"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="question">{{'question.question'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('question')"
                             class="help text-danger">{{
                        errors.first('question')
                        }}
                      </small>
                    </div>
                    </div>
            </div>
          </div>

          <div class="row row-reverse">
            <div class="col text-right">
              <label>پاسخ</label>
              <Vueditor ref="editor" style="height: 500px;"/>
            </div>
          </div>

          <div class="row row-reverse mt-4">
            <div class="col-md-3 text-right">
              <label>نمایش در صفحه نخست
                <input type="checkbox" v-model="showhomepage">
              </label>
            </div>
          </div>          

          <div class="row mt-5">
            <div class="col text-center">
              <button class="btn btn-success"  @click="add">
                {{'buttons.submit' | translate}}
              </button>
            </div>
          </div>          
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import api from 'src/packages/Api'
  import VueTagsInput from '@johmun/vue-tags-input'  

  export default {
    name: 'create-question',
    data () {
      return {
        title: '',
        question: '',
        answer: '',
        showhomepage:0,

        category_id: [],
        categoriesItems: [],
        question_type:[],
        question_typeItem:''
      }
    },
    components: {
      VueTagsInput
    },
    created () {
      api.get('questions/question_type').then(question_type => {
        const res = question_type
        for (let t in res) {
          this.question_type.push({
            id:t,
            title:res[t]
          })
        }
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: Vue.i18n.translate(`errors.${error.status}`) }
        )
        this.$router.push({ name: 'question_type' })
      })
   
      api.get('questions').then(response => {
        const res = response.data
        for (let t in res) {
          this.category_id.push({
            id:res[t].id,
            title:res[t].title
          })
        }
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: Vue.i18n.translate(`errors.${error.status}`) }
        )
        this.$router.push({ name: 'category_id' })
      })   
    },    
    methods: {
      add () {
        if (!this.question_type[0]) {
          this.$store.dispatch('addToastMessage', { text: Vue.i18n.translate('errors.category') })
        } else {
          this.$validator.validateAll().then((result) => {
            if (result) {                       
              const data = {
                title: this.title,
                question: this.question,
                answer: this.$refs.editor.getContent(),                
                home_show: this.showhomepage,
                category_id: this.categoriesItems,
                question_type: this.question_typeItem.id
              }

              api.post('questions', data).then(response => {
                this.$store.dispatch('ad.dToastMessage',
                  { text: Vue.i18n.translate('question.created'), type: 'success' }
                )
                this.$router.push({ name: 'question', params: { id: response.data.id } })
              }).catch(error => {
                if (error.status == 422){
                this.$store.dispatch('addToastMessage',
                  { text: error.data.message, error }
                )                 
                }else{
                this.$store.dispatch('addToastMessage',
                  { text: Vue.i18n.translate('errors.any'), error }
                )                  
                }

              })
            }
          })
        }
      }
    },
    watch: {
      
    }
  }
</script>

<style lang="scss">
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>
